import { Link } from "react-router-dom";
import ParticleBackground from "../background/Particles";
import "./Intro.css";
const Intro = () => {
  return (
    <>
      <ParticleBackground></ParticleBackground>
      <section className=" intro-page" id="home">
        <div className="small-intro">
          <div className="intro-row">
            <div
              className="col-lg-5  col-md-6 col-sm-12 intro-left"
              // style={{ textAlign: "left" }}
            >
              <div className="intro-name">
                <h3
                  className="hello"
                  data-aos="fade-down"
                  data-aos-duration="1500"
                >
                  Full Stack MERN Developer
                </h3>
                <h3
                  className="name"
                  data-aos="fade-down"
                  data-aos-duration="1600"
                >
                  Hey! I Am
                </h3>
                <h3
                  className="job  text-animate"
                  data-aos="fade-down"
                  data-aos-duration="1700"
                  // style={{ textAlign: "left" }}
                >
                  Salil Upadhyay
                </h3>
                <p
                  className="myinfo"
                  data-aos="fade-down"
                  data-aos-duration="1800"
                  // style={{ textAlign: "left" }}
                >
                  I am a full stack web developer. I enjoy building everything
                  from small business sites to rich interactive web apps.
                </p>
              </div>
              <div
                className="intro-btns"
                data-aos="fade-up"
                data-aos-duration="1900"
              >
                <Link to={"#"} className="contactMe">
                  <button className="contact-me">
                    Hire me <i class="bx bx-send "></i>
                  </button>
                </Link>
              </div>
              <div
                class="intro-contact"
                data-aos="fade-up"
                data-aos-duration="1800"
              >
                <span style={{ textAlign: "left" }}>Connect With Me:</span>
                <ul>
                  <li>
                    <Link
                      to={"https://twitter.com/salil-01"}
                      className="icon-link"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i class="fa fa-twitter" aria-hidden="true"></i>
                    </Link>
                    <Link
                      to={"https://instagram.com/salil-01"}
                      className="icon-link"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i class="fa fa-instagram" aria-hidden="true"></i>
                    </Link>
                    <Link
                      to={"https://linkedin.com/upadhyaysalil"}
                      className="icon-link"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i class="bx bxl-linkedin" aria-hidden="true"></i>
                    </Link>
                    <Link
                      to={"https://wa.me/8319712883?text="}
                      className="icon-link"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i class="bx bxl-whatsapp" aria-hidden="true"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
            <div
              className="col-lg-7 col-md-6 col-sm-12 left-img "
              data-aos="fade-down-left"
            >
              <div className="ff">
                <img className="intro-img" src="header-img2.svg" alt="" />
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Intro;
